h1 {
  font-weight: bold;
  color: white;
  margin: 6px 0 12px;
}

.date {
  color: var(--secondary);
  font-size: 14px;
  text-transform: uppercase;
}

 
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
 
}

.card-list {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

.card {
  -webkit-tap-highlight-color: transparent;
 display: inline-block;
  min-width: 350px !important;
  position: relative;
  padding: 0px;
  height: 400px;
  flex: 0 0 40%;
  max-width: 40%;
  margin-bottom: 100px;

}

 

