.card-content-container {
  width: 100%;
  height: 100%;
  position: relative;
  display: block;
  pointer-events: none;
 
}

.card-content-container.open {
  top: 0;
  left: 0;
  right: 0;
  position: fixed;
  z-index: 6;
  overflow: hidden;
  padding: 40px 0;
  overflow: scroll;
}

.card-content {
  pointer-events: auto;
  position: relative;
  border-radius: 20px;
  background: #fdfdfd;
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
::-webkit-scrollbar {
  width: 0;  /* Remove scrollbar space */
  background: transparent;  /* Optional: just make scrollbar invisible */
}
.open .card-content {
  height: auto;
  max-width: 40vw;
  width: auto;
  pointer-events: auto;
  height: 100vh;
}

.card-open-link {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  
}

.card-image-container {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  height: 420px;
  width: auto;
}

.open .card-image-container,
.open .title-container {
  z-index: 1;
   
}

.title-container {
  position: absolute;
  top: 15px;
  left: 15px;
  max-width: 300px;
  
}

.open .title-container {
  top: 30px;
  left: 30px;
}

h2 {
  color: #fff;
  margin: 8px 0;
}

.category {
  color: #fff;
  font-size: 14px;
  text-transform: uppercase;
  
}

.overlay {
  z-index: 1;
  position: fixed;
  background: rgba(0, 0, 0, 0.8);
  will-change: opacity;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 100vw;
  
  -webkit-tap-highlight-color: transparent;
}
 
.overlay a {
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100vw;
  left: 50%;

  transform: translateX(-50%);
}

.content-container {
  padding: 460px 35px 35px 35px;
  max-width: 700px;
  width: 90vw;
 
}

p {
  color: #9d9ca1;
  font-size: 20px;
  line-height: 28px;
  
}

